import React from 'react';
import { ADD_TO_CONTACTS, CLEAR_CONTACTS, CLEAR_SELECTION, DELETE_ALL_CONTACTS, DELETE_CONTACT, DELETE_CONTACTS, GET_CONTACT, SELECT_ALL_CONTACTS, SELECT_CONTACTS, UPDATE_CONTACT } from '../Constants/types';

export default function addToContacts(contact) {
    return {
        type:ADD_TO_CONTACTS,
        payload:contact
    }
}

export function getContact(id){
    return {
        type:GET_CONTACT,
        payload:id
    }
}

export function editContact(contact){
    return{
        type:UPDATE_CONTACT,
        payload:contact
    }
}

export function deleteContact(id){
    return {
        type:DELETE_CONTACT,
        payload:id
    }
}

export function selectAllContacts(id){
    return {
        type:SELECT_ALL_CONTACTS,
        payload:id
    }
}

export function clearSelection()
{
    return{
        type:CLEAR_SELECTION
    }
}

export function selectContacts(id){
    return {
        type:SELECT_CONTACTS,
        payload:id
    }
}

export function clearContacts(id){
    return{
        type:CLEAR_CONTACTS,
        payload:id
    }
}

export function deleteSelected(ids){
    return{
        type:DELETE_CONTACTS,
        payload:ids
    }
}

export function deleteAllContacts(){
    return{
        type:DELETE_ALL_CONTACTS
    }
}