import React, { useEffect, useState } from 'react';
import Avatar from 'react-avatar';
import { useDispatch } from 'react-redux';
import { Link, useNavigate } from 'react-router-dom';
import { clearContacts, deleteContact, selectContacts } from '../Actions/actions';
import Contacts from './contacts';

export default function Contact({contact, selectAll}) {
    const {firstname,lastname,email,phone,id} = contact;
    const navigate = useNavigate();
    const dispatch = useDispatch();
    const [selected, setSelected] = useState(false)
    useEffect(()=>{
        if(selected){
            dispatch(selectContacts(id));
        }
        else if(!selected)
            dispatch(clearContacts(id));
    },[selected])


    return <>
        <tr onClick = {()=>setSelected(!selected)}>
        <td scope='row'>
        <div className='custom-control custom-checkbox' >
          <input type="checkbox" checked = {selectAll|selected} className="custom-control-input"/>
          <label className="custom-control-label"></label>
        </div>
        </td>
        <td><Avatar className='mr-2' name={(firstname+" "+lastname)} size="45" round={true}></Avatar>{firstname+" "+lastname}</td>
        <td>{email}</td>
        <td>{phone}</td>
        <td>
            <Link to = {`/contacts/edit/${id}`} onClick = {()=>navigate(`/contacts/edit/${id}`)}>
                <span className='material-icons'>edit</span>            
            </Link>
            <Link to = "/">
                <span onClick={()=>dispatch(deleteContact(id))} className='material-icons'>remove_circle</span>            
            </Link>
        </td>
      </tr>
    </>
}