import React, { useEffect, useState } from 'react';
import { useDispatch, useSelector } from 'react-redux/es/exports';
import { selectAllContacts,deleteAllContacts, deleteSelected, clearSelection } from '../Actions/actions';
import Contact from './contact';

export default function Contacts() {
  const contacts = useSelector((state)=>state.contacts)
  const selectedContacts = useSelector((state)=>state.selectedContacts)
  const [selectAll, setselectAll] = useState(false)
  const dispatch = useDispatch();
  useEffect(()=>{
    if(selectAll)
    {dispatch(selectAllContacts(contacts.map((contact)=>contact.id)))}
    else if(!selectAll)
    {dispatch(clearSelection())}
  },[selectAll])

    return <div>
    {selectedContacts.length>0?(<button onClick={()=>{selectedContacts.length===contacts.length?dispatch(deleteAllContacts()):dispatch(deleteSelected(selectedContacts));if(selectAll)setselectAll(!selectAll)}} className='btn btn-danger mb-3 mt-3'>Delete Contacts</button>):null}
        <table className="table table-hover">
          <thead>
            <tr>
              <th>
                <div className='custom-control custom-checkbox'>
                  <input id = "selectAll" onClick = {()=>setselectAll(!selectAll)} value = {selectAll} checked = {selectAll} type="checkbox" className="custom-control-input"/>
                  <label htmlFor='selectAll' className="custom-control-label"></label>
                </div>
              </th>
              <th>Name</th>
              <th>Email</th>
              <th>Phone</th>
            </tr>
          </thead>
  <tbody>
    {contacts.map((contact)=>{
      return (<Contact contact = {contact} key = {contact.id} selectAll = {selectAll}/>)
    })}
  </tbody>
</table>
    </div>
}