import React, { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { useParams, useNavigate } from 'react-router-dom';
import addToContacts, { editContact, getContact } from '../Actions/actions';

export default function EditContact() {
    let {id} = useParams();
    let navigate = useNavigate();
    const dispatch = useDispatch();
    const contact = useSelector((state)=>state.contact);
    const [firstname, setFName] = useState("");
    const [lastname, setLName] = useState("");
    const [email, setEmail] = useState("");
    const [phone, setPhone] = useState(""); 

    useEffect(() => {
        if(contact!=null)
        {
            setFName(contact.firstname);
            setLName(contact.lastname);
            setEmail(contact.email);
            setPhone(contact.phone);
        }
        dispatch(getContact(id))
    }, [contact])
    
    function contactEditor(e){
        e.preventDefault();
        const edit_contact = Object.assign(contact,{
            firstname : firstname,
            lastname: lastname,
            email: email,
            phone: phone
        })
        dispatch(editContact(edit_contact));
        navigate("/")
    }   
    return <div className="container-fluid py-5">
    <form onSubmit={contactEditor}>
        <div className='form-group'>
        <label htmlFor="firstname"><strong>First name</strong></label>
        <input type="text" id="firstname" placeholder="First name" value ={firstname} onChange={(e)=>setFName(e.target.value)}/>
        </div>
        <div className='form-group'>
        <label  htmlFor="lastname"><strong>Last name</strong></label>
        <input type="text" id="lastname" placeholder="Last name" value ={lastname} onChange={(e)=>setLName(e.target.value)}/>
        </div>
        <div className='form-group'>
        <label htmlFor="email"><strong>Email</strong></label>
        <input type="text" id="email" placeholder="Email" value ={email} onChange={(e)=>setEmail(e.target.value)}/>
        </div>
        <div className='form-group'>
        <label htmlFor="phone"><strong>Phone</strong></label>
        <input type="text" id="phone" placeholder="Phone" value = {phone} onChange={(e)=>setPhone(e.target.value)}/>
        </div>
        <input type="submit" value = "Save" className="buttonSubmit"/>
    </form>
</div> 
}
