import React, { useState } from 'react'
import { useDispatch } from 'react-redux';
import { useNavigate } from 'react-router-dom';
import shortid from 'shortid';
import addToContacts from '../Actions/actions';

export default function AddContact() {
    const [firstname, setFName] = useState("");
    const [lastname, setLName] = useState("");
    const [email, setEmail] = useState("");
    const [phone, setPhone] = useState("");
    const dispatch = useDispatch();
    let navigate = useNavigate();
    function createContact(e){
        e.preventDefault();
        const new_contact = {
            id : shortid.generate(),
            firstname : firstname,
            lastname: lastname,
            email: email,
            phone: phone
        }
        dispatch(addToContacts(new_contact))
        navigate("/")
    }
    return <div className="container-fluid py-5">
            <form onSubmit={createContact}>
                <div className='form-group'>
                <label htmlFor="firstname"><strong>First name</strong></label>
                <input type="text" id="firstname" placeholder="First name" onChange={(e)=>setFName(e.target.value)}/>
                </div>
                <div className='form-group'>
                <label  htmlFor="lastname"><strong>Last name</strong></label>
                <input type="text" id="lastname" placeholder="Last name" onChange={(e)=>setLName(e.target.value)}/>
                </div>
                <div className='form-group'>
                <label htmlFor="email"><strong>Email</strong></label>
                <input type="text" id="email" placeholder="Email" onChange={(e)=>setEmail(e.target.value)}/>
                </div>
                <div className='form-group'>
                <label htmlFor="phone"><strong>Phone</strong></label>
                <input type="number" id="phone" placeholder="Phone" onChange={(e)=>setPhone(e.target.value)}/>
                </div>
                <input type="submit" value = "Add" className="buttonSubmit"/>
            </form>
    </div> 
}
