export default function contactReducer(state=initialState,action) {
    switch(action.type){
        case ADD_TO_CONTACTS: return{
          ...state,
          contacts:[...state.contacts, action.payload]
        } 
        case GET_CONTACT: let arr =state.contacts.filter((contact)=>contact.id===action.payload);
                          arr = arr.values();
                          for(let val of arr){
                            arr = val;
                          }
                          return {
                            ...state,
                            contact:arr
                          }
        case UPDATE_CONTACT: return{
                            ...state,
                            contacts:state.contacts.map((contact)=>contact.id===action.payload?action.payload:contact)}
                          
        case DELETE_CONTACT: return {
                          ...state,
                          contacts : state.contacts.filter((contact)=>contact.id!==action.payload)}
        case SELECT_ALL_CONTACTS:return{
                              ...state,
                              selectedContacts : action.payload
                            }
        case CLEAR_SELECTION: return{
                              ...state,
                              selectedContacts:[]
                            }  
        case DELETE_ALL_CONTACTS: return{
                                    ...state,
                                    contacts:[],
                                    selectedContacts : []
                                  }  
        case SELECT_CONTACTS : return{
                                    ...state,
                                    selectedContacts : [...state.selectedContacts,action.payload]
                                  }     
        case CLEAR_CONTACTS : return{
                                    ...state,
                                    selectedContacts : state.selectedContacts.filter((id)=>id!==action.payload)
                                }
        case DELETE_CONTACTS : return{
                                  ...state,
                                  contacts : state.contacts.filter((contact)=>state.selectedContacts.indexOf(contact.id)===-1),
                                  selectedContacts:[]
                                }        
        default : return state;
    }
}